import Movie from './Movie'
import React, { useState } from 'react'


const ListOfMovies = ({movies}) =>{
    const [movieToShow, setMovieToShow] = useState(null)
    const [average, setAverage] = useState(null)

    const select = (movie)=>
    {
        
        if(movie.title===movieToShow)
        setMovieToShow(null)
        else
        {
        setMovieToShow(movie.title)
        setAverage(movie.average)
        }
        
    }
    const style={
        margin:20
    }    
    const styleInner={
    marginLeft:20,
    marginTop:10
    }

return(
    <div style={style}>
<h2>Movies</h2>
<div style={styleInner}>
{movies.map((movie,index)=>
{
return <Movie key={index} movie={movie} toShow={movieToShow} selectToShow={select} average={average} />
})}
</div>
</div>
)}

export default ListOfMovies
