import React, { useState } from 'react'
import NumericInput from 'react-numeric-input';

import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';


const Review = ({ movie, getReview }) =>
{
    const [rating,setRating] = useState(5)
    const [writeReview,setWriteReview] = useState(``)
    
    const [buttonDisabled,setButtonDisable] = useState(false)

const reviewAdder = (event) =>
{
  event.preventDefault()
  if(writeReview.length > 3)
  {

  setButtonDisable(true)
 
// const review ={...movie,rating,review:writeReview}


const review = {Title:movie.Title,Year:movie.Year,Poster:movie.Poster,imdb:movie.imdbRating,reviews:[]}

console.log(review.imdb)
review.reviews.push({rating,review:writeReview})

console.log(review)
 getReview(review)

 setRating(5)
 setWriteReview("")

 setTimeout(() => {
  setButtonDisable(false)
}, 1000)
 
}
}

const left={
  display:'inline-block',
  float:"left",
  marginRight:110,
  marginLeft:50
}
const ReviewRight={
 // overflowX: "auto"
}



if(movie === null)
{
return null
}
else{

    return(
  <div>
  <div style={left}>
   <h2> {movie.Title} {movie.Year} </h2>
  <h3> IMDB-rating:{movie.imdbRating}</h3>
    <p>
    <img src={movie.Poster} alt="Poster"/>
     </p>
     </div>

     <h2>Review movie</h2>
     <Form  onSubmit={reviewAdder}>

     
<Form.Group controlId="reviewTextarea">
<Form.Row>
<Form.Label>Rating</Form.Label>
  <NumericInput required  min={0} max={10} step={0.1} value={rating} name="rating" onChange={setRating}/>
  </Form.Row>
  <Form.Row>

    <Form.Label>Write a review</Form.Label>
    <Form.Control placeholder="Write review here ( min 4 letters)" as="textarea" rows="17"  value={writeReview} 
         onChange={({ target }) =>setWriteReview(target.value)} />

       <Button variant="primary" type="submit">
          Save review
       </Button>    
    </Form.Row>
  </Form.Group>

    </Form>
    

  </div>
    )
}
}
/*

 <form style={ReviewRight} onSubmit={reviewAdder} >
      
      
      <h2>Review movie</h2>
    Rating <NumericInput required  min={0} max={10} step={0.1} value={rating} name="rating" onChange={setRating}/>

 
     <p> Review</p><p><textarea value={writeReview} name="review"  rows={18} cols={75} 
         onChange={({ target }) =>setWriteReview(target.value)}/>
  </p>
  <button type="submit"  disabled={buttonDisabled}>Save review</button>
</form>
*/



export default Review