import React, { useState } from 'react'
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';

const SearcMovieToReview = ({getSearchedTitle}) =>
{
    const [search, setsearch] = useState("")

    const getMovieAPI = (event)=>    //Haetaan eolkuvan tiedot omdbapi:sta
    {
      event.preventDefault()

      getSearchedTitle(search)
      
      setsearch("")
    }
    const margin={
      marginRight:10,
      marginTop:20
    }
  
    
    return(


      <Form style={margin} onSubmit={getMovieAPI}>
        <Form.Group controlId="formBasicMovie">
    <Form.Label>Search movie to review:</Form.Label>
    <Form.Control type="text" placeholder="Title of the movie" value={search}
          name="search"
          onChange={({ target }) => setsearch(target.value)} />
  </Form.Group>
  <Button variant="primary" type="submit">
    Seach movie
  </Button>
</Form>
    
      /*
    <div>
    
    <form onSubmit={getMovieAPI} style={left} >
      <h2>Search movie to review</h2>
    
         Title <input type="text" value={search} name="search"
         onChange={({ target }) =>setsearch(target.value)}/>
        <button type="submit">Search movie</button>
    </form>
    
    
    
  </div>
  */
    )
}
export default SearcMovieToReview